    border-radius: 15px;
    min-height: 400px;
    
    // 2-2   230:272   crop
    &.camera-22-frame {
        object-fit: cover;
        aspect-ratio: 272 / 230; // : 
        width: 100%;
        height: 100%;
    }
}

